#_primer-mq {
  font-family: "small=0&medium=40em&large=64em";
}

html {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-size: 0.875rem;
  line-height: 1.5;
  color: #27333b;
}

[type=reset],
[type=submit],
button,
html [type=button] {
  border: 0;
  -webkit-appearance: button;
}

a,
button {
  text-decoration: none;
}

.button {
  display: inline-block;
  padding: 0.5rem 1rem;
  color: #27333b;
  font-size: 0.875rem;
  font-weight: normal;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  user-select: none;
  background-color: white;
  border: 1px solid #27333b;
}

.button :focus {
  outline: -webkit-focus-ring-color auto 5px;
}

form .error-message {
  display: none;
}

form .error {
  position: relative;
}

form .error > .error-message {
  position: absolute;
  display: initial;
}

.container {
  position: relative;
  margin: 0 auto;
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
  max-width: 93.75rem;
}

.row {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.row:after {
  content: " ";
  display: block;
  clear: both;
}

.row-fluid {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.row .row {
  margin-left: -0.9375rem;
  margin-right: -0.9375rem;
}

