.sidebar {
  position: fixed;
  overflow: scroll;
  display: none;
  z-index: 20;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 1.25rem;
  width: 35%;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.26);
}

.sidebar--open {
  display: block;
}

.sidebar__title {
  margin: 0.3125rem 0rem 0.625rem;
  font-weight: bold;
  font-size: 1rem;
}

.sidebar hr {
  border: none;
  background-color: #ebebeb;
  height: 1px;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 15px;
}

.table thead {
  background-color: #27333b;
  color: white;
}

.table thead th {
  font-weight: normal;
  font-size: 1rem;
}

.table thead th.sortable {
  cursor: pointer;
}

.table tr td,
.table tr th {
  padding: 0.625rem;
  text-align: left;
}

.table tr td[align=right],
.table tr th[align=right] {
  text-align: right;
}

.table tbody tr {
  border-bottom: 1px solid #ebebeb;
}

.table tbody tr.selected, .table tbody tr:hover {
  background-color: #ebebeb;
}

.table tbody tr.selectable--row.selected {
  background-color: #ebebeb;
}

.table a:not(.button) {
  font-weight: bold;
}

.table a:not(.button):hover {
  text-decoration: underline;
}

.table span.sub-column {
  display: block;
}

.tag {
  vertical-align: middle;
  display: inline-block;
  width: 13px;
  height: 13px;
  background-color: #000;
  border-radius: 50%;
  margin-top: -1px;
  margin-right: 5px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

a {
  color: #0077c8;
}

.modal[role="dialog"] {
  display: none;
  position: fixed;
  background: rgba(0, 0, 0, 0.8);
  overflow: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.modal[role="dialog"].modal--visible {
  display: block;
}

.modal[role="dialog"].modal--visible .modal-dialog {
  max-height: calc(100vh - 80px);
  overflow: auto;
  position: absolute;
  left: 25%;
  top: 25%;
  margin-bottom: 40px;
  width: 50%;
  background: white;
}

.modal[role="dialog"].modal--visible .modal-dialog .modal-content {
  position: relative;
  padding: 15px;
}

.modal[role="dialog"].modal--visible .modal-dialog .modal-content .modal-header {
  background: #27333b;
  position: absolute;
  padding: 0 15px;
  left: 0;
  top: 0;
  right: 0;
  height: 40px;
}

.modal[role="dialog"].modal--visible .modal-dialog .modal-content .modal-header * {
  float: left;
}

.modal[role="dialog"].modal--visible .modal-dialog .modal-content .modal-header .close {
  position: absolute;
  top: 0;
  right: 0;
  height: 40px;
  background: black;
  color: white;
}

.modal[role="dialog"].modal--visible .modal-dialog .modal-content .modal-header .modal-title {
  margin: 0;
  color: white;
  line-height: 40px;
}

.modal[role="dialog"].modal--visible .modal-dialog .modal-content .modal-body {
  margin-top: 40px;
}

.select2-container .select2-selection--single,
.select2-container--default .select2-selection--single .select2-selection__arrow {
  height: 2.3125rem;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 2.3125rem;
}

.select2-container--default .select2-selection--single {
  border-radius: 3px;
  border: 1px solid #d5d5d5;
}

.select2-container {
  margin-bottom: 15px;
}

.select2-selection--multiple input.select2-search__field[type=search] {
  margin-bottom: auto;
}

